/* 基础重置
---------------------*/
a, abbr, address, article, aside, audio, b, blockquote,button, body, caption, cite, code, dd, del, dfn, dialog, div, dl, dt, em, fieldset, figure, footer, form, h1, h2, h3, h4, h5, h6, header, hgroup, hr, html, i, iframe, img, ins, kbd, label, legend, li, mark, menu, nav, object, ol, p, pre, q, samp, section, small, span, strong, sub, sup, table, tbody, td, tfoot, th, thead, time, tr, ul, var, video, textarea, input{border: 0; margin: 0; outline: 0; padding: 0; font-size: 100%;-webkit-box-sizing: border-box;box-sizing: border-box;}
a:hover{text-decoration:none;}
a{text-decoration:none;}
html, body{-webkit-text-size-adjust: none; font-family:"microsoft yahei";min-width:1200px;background:#f5f5f5;/*重置chrome字体*/}
/* -- 去除chrome浏览器默认设置 -- */
textarea{resize: none; overflow: auto;}
input, button, select, textarea{outline: none; border:none;background-color: transparent;font-family:"microsoft yahei";}
input, select{vertical-align: middle; border: 1px solid #ececec;}
b, strong{font-weight: bold;}
img{font-size: 0; border: none; vertical-align: top;}
a{color:#666;}
table{border-collapse: collapse; border-spacing: 0;}
th, td, caption{vertical-align: middle; text-align: left;}
ul, li{list-style: none;}
i{
    font-style:normal;
}
h1,h2,h3,h4,h5,h6{font-weight:normal;}
::-webkit-input-placeholder { /* WebKit browsers */
    color:#BBB;
    font-family:"microsoft yahei";
    font-size:14px!important;
}
:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
    color:#BBB;
    font-family:"microsoft yahei";
    font-size:14px!important;
}
::-moz-placeholder { /* Mozilla Firefox 19+ */
    color:#BBB;
    font-family:"microsoft yahei";
    font-size:14px!important;
}
